<template>
  <div>
    <v-container fluid grid-list-md>
      <v-card>
        <v-card-title>
          <v-layout row wrap>
            <v-flex xs2>
              <file-drag @onFileRead="onReadFile"></file-drag>
            </v-flex>
            <v-flex xs2>
              <v-card>
                <file-download :data="downloaddata"></file-download>
              </v-card>
            </v-flex>
            <v-flex xs2>
              <v-card>
                <v-btn @click="addSpell"></v-btn>
              </v-card>
            </v-flex>
            <v-flex xs6>
              <v-text-field append-icon="search" label="Input" single-line v-model="conditon.spellname" hint="" persistent-hint></v-text-field>
            </v-flex>
            <v-flex xs3>
              <v-select label="Select" :items="levels" v-model="conditon.levels" multiple max-height="400" hint="" persistent-hint></v-select>
            </v-flex>
            <v-flex xs3>
              <v-select label="Select" :items="classes" v-model="conditon.classes" multiple max-height="400" hint="" persistent-hint></v-select>
            </v-flex>
            <v-flex xs3>
              <v-select :items="rituals" v-model="conditon.ritual" max-height="400" hint="" persistent-hint></v-select>
            </v-flex>
            <v-flex xs3>
              <v-select label="Select" :items="components" v-model="conditon.components" multiple max-height="400" hint="" persistent-hint></v-select>
            </v-flex>
          </v-layout>
        </v-card-title>
        <v-data-table :headers="headers" :items="items" item-key="name" no-data-text="">
          <template slot="items" slot-scope="props">
            <tr @click="clickCell(props)">
              <td class="text-xs-right">{{ props.item.name }}({{props.item.page}})</td>
              <td class="text-xs-right">{{ formatArray(props.item.class,classes)}}</td>
              <td class="text-xs-right">{{ props.item.level }}</td>
              <td class="text-xs-right">{{ formatArray(props.item.components,components) }}</td>
              <td class="text-xs-right">{{ props.item.casting_time }}</td>
              <td class="text-xs-right">{{ props.item.duration }}</td>
              <td class="text-xs-right">{{ format(props.item.concentration,concentration) }}</td>
              <td class="text-xs-right">{{ props.item.range }}</td>
              <td class="text-xs-right">{{ format(props.item.ritual,rituals) }}</td>
            </tr>
          </template>
          <template slot="expand" slot-scope="props">
            <v-card>
              <v-layout row wrap>
                <v-flex xs3>
                  <v-subheader class="body-2"></v-subheader>
                </v-flex>
                <v-flex xs3>
                  <v-subheader class="body-2"></v-subheader>
                </v-flex>
                <v-flex xs3>
                  <v-subheader class="body-2"></v-subheader>
                </v-flex>
                <v-flex xs2>
                  <v-subheader class="body-2"></v-subheader>
                </v-flex>
                <v-flex xs1>
                  <v-btn color="primary" fab small dark @click="editSpell(props.item)">
                    <v-icon>edit</v-icon>
                  </v-btn>
                </v-flex>
                <v-flex xs3>
                  <v-card-text class="body-2">{{ formatArray(props.item.class,classes)}}</v-card-text>
                </v-flex>
                <v-flex xs3>
                  <v-card-text class="body-2">{{ props.item.level }}</v-card-text>
                </v-flex>
                <v-flex xs3>
                  <v-card-text class="body-2">{{ formatArray(props.item.components,components) }} {{props.item.materialdeteil}}</v-card-text>
                </v-flex>
                <v-flex xs3>
                  <v-card-text class="body-2">{{ props.item.casting_time }}</v-card-text>
                </v-flex>
                <v-flex xs3>
                  <v-subheader class="body-2"></v-subheader>
                </v-flex>
                <v-flex xs3>
                  <v-subheader class="body-2"></v-subheader>
                </v-flex>
                <v-flex xs3>
                  <v-subheader class="body-2"></v-subheader>
                </v-flex>
                <v-flex xs3>
                  <v-subheader class="body-2"></v-subheader>
                </v-flex>
                <v-flex xs3>
                  <v-card-text class="body-2">{{ props.item.duration }}</v-card-text>
                </v-flex>
                <v-flex xs3>
                  <v-card-text class="body-2">{{ format(props.item.concentration,concentration) }}</v-card-text>
                </v-flex>
                <v-flex xs3>
                  <v-card-text class="body-2">{{ props.item.range }}</v-card-text>
                </v-flex>
                <v-flex xs3>
                  <v-card-text class="body-2">{{ format(props.item.ritual,rituals) }}</v-card-text>
                </v-flex>
                <v-flex xs12>
                  <v-divider/>
                  <v-card-text class="body-2">
                    <span style="white-space: pre-wrap;" v-html="props.item.desc"></span>
                  </v-card-text>
                </v-flex>
              </v-layout>
            </v-card>
          </template>
        </v-data-table>
      </v-card>
    </v-container>

    <!---->
    <v-dialog v-model="showDialog" persistent>
      <v-container grid-list-md>
        <v-card color="grey lighten-4" flat v-if="editspell!=null">
          <v-card-text>
            <v-container fluid>
              <v-form ref="form">
                <v-layout row wrap>
                  <v-flex xs2>
                    <v-text-field name="" type="text" label="" v-model="editspell.name" required />
                  </v-flex>
                  <v-flex xs2>
                    <v-card-text>({{editspell.page}})</v-card-text>
                  </v-flex>
                  <v-flex xs3>
                    <v-select label="Select" :items="classes" v-model="editspell.class" multiple max-height="400" hint="" persistent-hint></v-select>
                  </v-flex>
                  <v-flex xs3>
                    <v-select label="Select" :items="levels" v-model="editspell.level" max-height="400" hint="" persistent-hint></v-select>
                  </v-flex>
                  <v-flex xs3>
                    <v-select label="Select" :items="components" v-model="editspell.components" multiple max-height="400" hint="" persistent-hint></v-select>
                  </v-flex>
                  <v-flex xs3>
                    <v-text-field name="" type="text" label="" v-model="editspell.materialdeteil" />
                  </v-flex>
                  <v-flex xs3>
                    <v-text-field name="" label="" v-model="editspell.casting_time" required />
                  </v-flex>
                  <v-flex xs3>
                    <v-text-field name="" label="" v-model="editspell.duration" required />
                  </v-flex>
                  <v-flex xs2>
                    <v-select label="Select" :items="concentration" v-model="editspell.concentration" max-height="400" hint="" persistent-hint></v-select>
                  </v-flex>
                  <v-flex xs2>
                    <v-text-field name="" label="" v-model="editspell.range" required />
                  </v-flex>
                  <v-flex xs2>
                    <v-select label="Select" :items="enterRitual" v-model="editspell.ritual" max-height="400" hint="" persistent-hint></v-select>
                  </v-flex>
                  <v-flex xs12>
                    <v-text-field class="nowrap" name="" label="" v-model="editspell.desc" multi-line required />
                  </v-flex>
                </v-layout>
              </v-form>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" flat @click.native="save"></v-btn>
            <v-btn color="green darken-1" flat @click.native="cancel"></v-btn>
            <v-btn color="green darken-1" flat @click.native="remove"></v-btn>
          </v-card-actions>
        </v-card>
      </v-container>
    </v-dialog>

  </div>
</template>

<style>

</style>
</style>

<script>
import spells from "@/model/spells";
import FileDrag from "@/components/FileDrag";
import FileDownload from "@/components/FileDownload";

export default {
  name: "SearchSpellTable",
  components: {
    FileDrag: FileDrag,
    FileDownload: FileDownload
  },
  data() {
    return {
      //
      spelldata: [],
      //
      conditon: {
        spellname: "",
        levels: [],
        classes: [],
        ritual: null,
        components: []
      },
      //
      levels: [
        "Cantrip",
        "1st-level",
        "2nd-level",
        "3rd-level",
        "4th-level",
        "5th-level",
        "6th-level",
        "7th-level",
        "8th-level",
        "9th-level"
      ],
      classes: [
        { text: "", value: "Bard" },
        { text: "", value: "Cleric" },
        { text: "", value: "Druid" },
        { text: "", value: "Paladin" },
        { text: "", value: "Ranger" },
        { text: "", value: "Sorcerer" },
        { text: "", value: "Wizard" },
        { text: "", value: "Warlock" }
      ],
      rituals: [
        { text: "", value: null },
        { text: "", value: "yes" },
        { text: "", value: "no" }
      ],
      components: [
        { text: "(S)", value: "S" },
        { text: "(V)", value: "V" },
        { text: "(M)", value: "M" }
      ],
      headers: [
        { text: "", value: "name", align: "right" },
        { text: "", value: "class", align: "right" },
        { text: "", value: "level", align: "right" },
        { text: "", value: "components", align: "right" },
        { text: "", value: "casting_time", align: "right" },
        { text: "", value: "duration", align: "right" },
        { text: "", value: "concentration", align: "right" },
        { text: "", value: "range", align: "right" },
        { text: "", value: "ritual", align: "right" }
      ],
      concentration: [
        { text: "", value: "yes" },
        { text: "", value: "no" }
      ],
      enterRitual: [
        { text: "", value: "yes" },
        { text: "", value: "no" }
      ],
      //
      showDialog: false,
      //
      editspell: {},
      //
      editedspell: null
    };
  },
  beforeMount() {
    //
    this.spelldata = spells();
  },
  computed: {
    //
    items() {
      return (
        this.spelldata
          // .slice(1, 10) //for debug.
          .filter(element => {
            if (!this.filterSpellname(element)) {
              return false;
            }
            if (!this.fliterRitual(element)) {
              return false;
            }
            if (!this.fliterLevels(element)) {
              return false;
            }
            if (!this.fliterClasses(element)) {
              return false;
            }
            if (!this.fliterComopnents(element)) {
              return false;
            }
            return true;
          })
      );
    },
    //
    downloaddata() {
      return JSON.stringify(this.spelldata);
    }
  },
  methods: {
    //computed
    fliterRitual(element) {
      if (this.conditon.ritual === null) {
        return true;
      } else {
        return element.ritual === this.conditon.ritual;
      }
    },

    fliterLevels(element) {
      if (this.conditon.levels.length === 0) {
        return true;
      } else {
        return this.conditon.levels.some(e => {
          return e === element.level;
        });
      }
    },
    fliterClasses(element) {
      if (this.conditon.classes.length === 0) {
        return true;
      } else {
        return this.conditon.classes.some(e => {
          return element.class.includes(e);
        });
      }
    },
    //FIXME 
    fliterComopnents(element) {
      if (this.conditon.components.length === 0) {
        return true;
      } else {
        if (element.components.length !== this.conditon.components.length) {
          return false;
        }
        for (const s of element.components) {
          if (this.conditon.components.indexOf(s) === -1) {
            return false;
          }
        }
        return true;
      }
    },
    filterSpellname(element) {
      var trimed = this.conditon.spellname.trim();
      if (trimed === "") {
        return true;
      } else {
        //FIXME includes
        return element.name.includes(trimed);
        //return element.name.startsWith(trimed);
      }
    },
    //JSON
    onReadFile(files) {
      for (const file of files) {
        let fileReader = new FileReader();
        fileReader.onload = data => {
          let json = data.target.result;
          try {
            let spells = JSON.parse(json);
            this.spelldata = spells;
          } catch (e) {
            //FIXME
            alert(e);
          }
        };
        fileReader.readAsText(file);
      }
    },

    //JSON
    // download() {
    //   var debug = { hello: "world" };
    //   var blob = new Blob([JSON.stringify(debug, null, 2)], {
    //     type: "application/octet-stream"
    //   });

    //   let downloadlink = this.$refs.downloadlink;
    //   downloadlink.href = URL.createObjectURL(blob);
    //   downloadlink.click();
    // },

    //expand
    clickCell(props) {
      props.expanded = !props.expanded;
    },

    //expand
    editSpell(spell) {
      this.editedspell = spell;
      Object.assign(this.editspell, spell);
      this.showDialog = true;
    },

    //
    addSpell() {
      this.showDialog = true;
    },

    //
    save() {
      if (this.editedspell == null) {
        this.spelldata.push(this.editspell);
        this.editspell = {};
        this.showDialog = false;
      } else {
        Object.assign(this.editedspell, this.editspell);
        this.showDialog = false;
        this.editedspell = null;
        this.editspell = {};
      }
    },

    //
    cancel() {
      this.showDialog = false;
      this.editspell = {};
      this.editedspell = null;
    },

    //
    remove() {
      this.showDialog = false;
      let index = this.spelldata.indexOf(this.editedspell);
      this.spelldata.splice(index, 1);
      this.editspell = {};
      this.editedspell = null;
    },

    //val: tablevaluetext
    format(val, table) {
      var retVal = val;
      // alert(val);
      table.forEach(element => {
        // alert(element.value);
        // alert(val);
        if (element.value === val) {
          retVal = element.text;
          return;
        }
      });
      return retVal;
    },

    //
    formatArray(val, table) {
      var array = [];
      val.forEach(element => {
        array.push(this.format(element, table));
      });
      return array.join(",");
    }
  }
};
</script>



