
<template>
  <div>
    <!---->
    <v-layout row wrap>
      <v-flex xs12 md6 offset-md3>
        <v-card>
          <v-data-table v-bind:headers="headers" :items="items" hide-actions :total-items="totalItems" class="elevation-1">
            <template slot="items" slot-scope="props">
              <td class="text-xs-right">{{ props.item.charctername }}</td>
              <td class="text-xs-right">{{ props.item.initiative }}</td>
              <td class="text-xs-right">{{ props.item.initiativemodifier }}</td>
              <td>
                <v-tooltip top>
                  <v-btn color="primary" fab small dark slot="activator" @click="openEditDialig(props.item)">
                    <v-icon>edit</v-icon>
                  </v-btn>
                  <!--activator-->
                  <span></span>
                </v-tooltip>
                <v-tooltip top>
                  <v-btn color="secondary" fab small dark slot="activator" @click="deleteItem(props.item)">
                    <v-icon>delete</v-icon>
                  </v-btn>
                  <span></span>
                </v-tooltip>
              </td>
            </template>
          </v-data-table>
        </v-card>
        <v-card color="indigo lighten-4">
          <v-tooltip top>
            <v-btn color="primary" fab small dark slot="activator" @click="openAddDialig()">
              <v-icon>add</v-icon>
            </v-btn>
            <span></span>
          </v-tooltip>
          <v-tooltip top>
            <v-btn color="secondary" fab small dark slot="activator" @click="sort()">
              <v-icon>sort</v-icon>
            </v-btn>
            <span></span>
          </v-tooltip>
          <v-tooltip top>
            <v-btn color="accent" fab small dark slot="activator" @click="clearInitiative()">
              <v-icon>clear</v-icon>
            </v-btn>
            <span></span>
          </v-tooltip>
        </v-card>
      </v-flex>
    </v-layout>

    <!---->
    <v-dialog v-model="dialog" persistent max-width="480">
      <v-card color="grey lighten-4" flat>
        <v-card-text>
          <v-container fluid>
            <v-form ref="form">
              <v-text-field name="" label="" v-model="editcharacter.charctername" :rules="charcternameRule" required />
              <v-text-field name="" label="" type="number" v-model.number="editcharacter.initiativemodifier" :rules="initiativemodifierRule" required/>
              <v-text-field name="" label="" type="number" v-model.number="editcharacter.initiative" :rules="initiativeRule" />
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click.native="save"></v-btn>
          <v-btn color="green darken-1" flat @click.native="cancel"></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!---->
    <v-snackbar timeout=1000 :color="snackbarcolor" vertical=true v-model="snackbar">
      {{ snackbarmessage }}
      <v-btn dark flat @click.native="snackbar = false">Close</v-btn>
    </v-snackbar>
  </div>
</template>

<style>

</style>

<script>
export default {
  // 
  name: "InitiativeBoard",
  data() {
    return {
      // totalItems 
      totalItems: 2,
      // // 
      headers: [
        {
          text: "",
          value: "charctername",
          align: "right",
          sortable: false,
          width: "40%"
        },
        {
          text: "",
          value: "initiative",
          align: "right",
          sortable: false,
          width: "15%"
        },
        {
          text: "",
          value: "initiativemodifier",
          align: "right",
          sortable: false,
          width: "15%"
        },
        {
          text: "",
          value: "action",
          align: "right",
          sortable: false,
          width: "30%"
        }
      ],
      // 
      items: [
        {
          charctername: "A",
          initiative: 1,
          initiativemodifier: 1
        },
        {
          charctername: "B",
          initiative: 2,
          initiativemodifier: 0
        }
      ],

      // itemitemsitemnull
      edititem: null,
      // 
      editcharacter: {
        charctername: "",
        initiative: 0,
        initiativemodifier: 0
      },
      // 
      dialog: false,
      // 
      snackbar: false,
      snackbarcolor: "error",
      snackbarmessage: "",
      // 
      // FIXME VeeValidate 
      // https://qiita.com/acro5piano/items/2be6068b0647ecffcd86
      // https://vuetifyjs.com/components/forms #4 Vee-validate
      charcternameRule: [v => !!v || ""],
      initiativemodifierRule: [
        v => v !== "" || "", // .numberString
        v => !isNaN(v) || ""
      ],
      initiativeRule: [
        v => !v || !isNaN(v) || ""
      ]
    };
  },
  methods: {
    // 
    sort() {
      // 1.
      var check = this.items.some(c => {
        return c.initiative === undefined;
      });
      if (check) {
        this._showSnackbar(
          "error",
          ""
        );
      } else {
        this.items.sort((a, b) => {
          if (a.initiative < b.initiative) {
            return 1;
          } else if (a.initiative > b.initiative) {
            return -1;
          } else if (a.initiativemodifier < b.initiativemodifier) {
            return 1;
          } else {
            return 0;
          }
        });
        this._showSnackbar("success", "");
      }
    },
    // 
    clearInitiative() {
      this.items.forEach(i => (i.initiative = undefined));
    },
    // 
    openEditDialig(item) {
      this.edititem = item;
      Object.assign(this.editcharacter, item);
      this.dialog = true;
    },
    // 
    openAddDialig() {
      this.edititem = null;
      this.editcharacter = {};
      this.dialog = true;
    },
    // 
    deleteItem(item) {
      var index = this.items.indexOf(item);
      if (index >= 0) {
        this.items.splice(index, 1);
      }
      this._showSnackbar("success", "");
    },
    // 
    // items
    // item
    // edititemnull
    save() {
      if (!this.$refs.form.validate()) {
        // 
      } else {
        if (this.edititem != null) {
          this.dialog = false;
          Object.assign(this.edititem, this.editcharacter);
          this._clear();
        } else {
          this.dialog = false;
          this.items.push(this.editcharacter);
          this._clear();
        }
      }
    },
    // 
    // 
    cancel() {
      this._clear();
      this.dialog = false;
    },
    // 
    _clear() {
      this.editcharacter = {};
      this.edititem = null;
    },
    // 
    _showSnackbar(color, message) {
      this.snackbarmessage = message;
      this.snackbarcolor = color;
      this.snackbar = true;
    }
  }
};
</script>


